.mapContainer {
  width: 125vh;
  height: 75vh;
  border-radius: 15px;
  align-self: center;
  justify-content: center;
}

.infocords {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100vh;
  background-color: grey;
  border-radius: 15px;
  color: white;
  left: 30px;
  padding: 10px;
  top: 20px;
  z-index: 99;
  font-family: Arial, Helvetica, sans-serif;
  position: fixed;
}


